<template>
  <div id='app'>
    <header class='top-header'>
      <div class='header_top'>
        <div class='header_column'>
          <div class='header_time'>
            <p>12:50</p>
          </div>
        </div>

        <div class='header_column'>
          <i class='fas fa-bell'></i>
          <i class='fas fa-wifi'></i>
          <i class='fab fa-bluetooth-b'></i>
          <span class='header_battery'>
            80%
            <i class='fas fa-battery-three-quarters'></i>
          </span>
        </div>
      </div>
      <div class='header_bottom'>
        <div class='header_column'>
          <span class='header_text'>{{menu}}</span>
        </div>
        <div class='header_column'>
          <i :class='icon1'></i>
          <i :class='icon2'></i>
          <i :class='icon3'></i>
          <i :class='icon4'></i>
        </div>
      </div>
    </header>

    <router-view></router-view>

    <nav class='tab-bar'>
      <router-link to='/friends' class='tab-bar_tab'>
        <i class='far fa-user'></i>
      </router-link>
      <router-link to='/chats' class='tab-bar_tab'>
        <i class='far fa-comment'></i>
      </router-link>
      <router-link to='/news' class='tab-bar_tab'>
        <i class='fas fa-hashtag'></i>
      </router-link>
      <router-link to='/more' class='tab-bar_tab'>
        <i class='fas fa-ellipsis-h'></i>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      menu: '',
      icon1: '',
      icon2: '',
      icon3: '',
      icon4: ''
    }
  },
  destroy () {
    this.$off('changeData')
  },
  created () {
    const _self = this

    this.$on('changeData', function (
      newMenu,
      newIcon1,
      newIcon2,
      newIcon3,
      newIcon4
    ) {
      _self.menu = newMenu
      _self.icon1 = newIcon1
      _self.icon2 = newIcon2
      _self.icon3 = newIcon3
      _self.icon4 = newIcon4
    })
  }
}
</script>

